.body-main__text {
  color: white;
  font-size: 3vw;
  -webkit-text-stroke: 0.5px black;
  max-width: 80vw;
  margin-top: 5vh;
  margin-bottom: 2.5vh;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 10;
  -webkit-box-orient: vertical;
}

@media screen and (max-width: 700px) {
  .body-main__text {
    font-size: 5vw;
  }
}
