.body-main__link {
  color: white;
  font-size: 5vw;
  -webkit-text-stroke: 2px black;
  max-width: 50vw;
  margin-top: 5vh;
  margin-bottom: 0;
  text-align: center;
  text-decoration: none;
}

.body-main__link:hover {
  opacity: 0.3;
}

@media screen and (max-width: 899px) {
  .body-main__link {
    font-size: 10vw;
  }
}
